/* eslint-disable */
import "../App.css";
import { FullPage, Slide } from "react-full-page";

const StartPage = () => {
    return (
        <div className="start_page">
            <FullPage>
                <Slide className="slide_page first">
                    <h1>YeungJin Japan IT</h1>
                    <div className="slide_container">
                        <p>
                            취업률 100%의 신화, 영진전문대 Japan IT
                            웹사이트입니다
                        </p>
                        <a href="">
                            <button className="slide_button no"></button>
                        </a>
                    </div>
                </Slide>
                <Slide className="slide_page second">
                    <h1>Our User</h1>
                    <div className="slide_container">
                        <p>웹페이지를 만든 조원들을 소개합니다</p>
                        <a href="/profile">
                            <button className="slide_button">Go</button>
                        </a>
                    </div>
                </Slide>
                <Slide className="slide_page third">
                    <h1>PHOTOS</h1>
                    <div className="slide_container">
                        <p>후쿠오카에서의 추억들을 담았습니다</p>
                        <a href="/album">
                            <button className="slide_button">Go</button>
                        </a>
                    </div>
                </Slide>
                <Slide className="slide_page fourth">
                    <h1>Calendar</h1>
                    <div className="slide_container">
                        <p>현지학기제의 일정은 어떻게 될까요?</p>
                        <a href="/cal">
                            <button className="slide_button">Go</button>
                        </a>
                    </div>
                </Slide>
                <Slide className="slide_page fifth">
                    <h1>Community</h1>
                    <div className="slide_container">
                        <p>함께 소통하고 지식을 나눠요</p>
                        <a href="/notice/1">
                            <button className="slide_button">Go</button>
                        </a>
                    </div>
                </Slide>
            </FullPage>
        </div>
    );
};

export default StartPage;
